const Footer = () => {
  const { t, i18n } = useTranslation();
  const currentYear = new Date().getFullYear();

  useEffect(() => {
    // This ensures the footer's direction matches the global language direction
    // No specific action needed here if the parent component or html tag handles direction
  }, [i18n, i18n.language]);

  return (
    <FooterContainer dir={i18n.dir(i18n.language)}>
      <CopyrightText>
        {t('copyright', { year: currentYear })}
      </CopyrightText>
      <div>
        {/* Optional: Add sitemap or other links here if needed */}
        {/* <FooterLink href="#privacy">Privacy Policy</FooterLink>
        <FooterLink href="#terms">Terms of Service</FooterLink> */}
      </div>
    </FooterContainer>
  );
};

export default Footer;

