import React, { useEffect } from 'react';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import logo from '../assets/images/logo.png';

const HeaderContainer = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  direction: ${props => props.dir};

  @media (max-width: 768px) {
    flex-direction: column;
    padding: 1rem;
  }
`;

const LogoLink = styled.a`
  display: inline-block; // Prevents layout shift if logo loads late
`;

const Logo = styled.img`
  height: 50px;
  @media (max-width: 768px) {
    margin-bottom: 1rem;
  }
`;

const Nav = styled.nav`
  display: flex;
  gap: 1.5rem;
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
    width: 100%;
  }
`;

const NavLink = styled.a` // Will be React Router's Link
  text-decoration: none;
  color: #333;
  font-weight: 500;
  padding: 0.5rem 0;
  &:hover {
    color: #007bff;
  }
`;

const ControlsContainer = styled.div`
  display: flex;
  align-items: center;
  gap: 1rem;
   @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
  }
`;

const LanguageToggle = styled.button`
  background: none;
  border: 1px solid #ccc;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-weight: 500;
  &:hover {
    background-color: #f0f0f0;
  }
`;

const WhatsAppLink = styled.a`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
  color: #25D366; // WhatsApp green
  font-weight: 500;
  padding: 0.5rem 1rem;
  border: 1px solid #25D366;
  border-radius: 4px;

  &:hover {
    background-color: #25D366;
    color: white;
  }

  img { // If using an icon image
    height: 20px;
    width: 20px;
  }
`;

const Header = () => {
  const { t, i18n } = useTranslation();

  const toggleLanguage = () => {
    const newLang = i18n.language === 'en' ? 'ar' : 'en';
    i18n.changeLanguage(newLang);
  };

  useEffect(() => {
    document.documentElement.lang = i18n.language;
    document.documentElement.dir = i18n.dir(i18n.language);
  }, [i18n, i18n.language]);

  return (
    <HeaderContainer dir={i18n.dir(i18n.language)}>
      <LogoLink href="#home">
        <Logo src={logo} alt="Cosmos Pharma Logo" />
      </LogoLink>
      <Nav>
        <NavLink href="#home">{t('navHome')}</NavLink>
        <NavLink href="#about">{t('navAbout')}</NavLink>
        <NavLink href="#products">{t('navProducts')}</NavLink>
        <NavLink href="#partners">{t('navPartners')}</NavLink>
        <NavLink href="#contact">{t('navContact')}</NavLink>
      </Nav>
      <ControlsContainer>
        <LanguageToggle onClick={toggleLanguage}>
          {i18n.language === 'en' ? '' : 'English'}
        </LanguageToggle>
        <WhatsAppLink href="https://wa.me/966599977742" target="_blank" rel="noopener noreferrer">
          {/* Placeholder for WhatsApp icon - will use an actual icon or SVG later */}
          {t('whatsapp')}
        </WhatsAppLink>
      </ControlsContainer>
    </HeaderContainer>
  );
};

export default Header;

